import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {Logger} from "@util/logger";
import {DecommissionModeSummary} from "@component/precheck/pre-check-evacuation-summary.component";
import {DecommissionMode} from "@generated/decommission-mode";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {PreCheckEvacuationDialogSize} from "@component/precheck/pre-check-evacuation-dialog-size";

@Component({
   templateUrl: "pre-check-evacuation-dialog.html",
   selector: "vsan-pre-check-evacuation-dialog",
})
export class PreCheckEvacuationDialogComponent implements OnInit {
   hostMor: ManagedObject;
   clusterId: string = "";
   evacuationEntityUuid: string = "";
   entityName: string;
   decommissionMode: DecommissionMode;
   primaryTitle: string = "";
   secondaryTitle: string = "";
   isDecommissionModeChangeAllowed: boolean = true;

   async ngOnInit() {
      const context: any = VsanUiUtils.getModalContext();
      this.hostMor = context.hostMor;
      this.evacuationEntityUuid = context.objectUid;
      this.entityName = context.objectName;
      this.clusterId = context.clusterId;
      if (context.selectedDecommisionMode) {
         // Dialog is opened from the context of Enter Maintenance Mode and has preselected decommission mode
         // Showing the "Detailed report" dialog here, which doesn't allow changing the selected decommission mode
         this.isDecommissionModeChangeAllowed = false;
         this.primaryTitle = VsanUiUtils.getString("vsan.dialog.precheckEvacuation.detailedReport.title");
         switch (context.selectedDecommisionMode) {
            case "evacuateAllData":
               this.decommissionMode = DecommissionMode.evacuateAllData;
               this.secondaryTitle = VsanUiUtils.getString(
                     "vsan.dialog.precheckEvacuation.detailedReport.secondaryTitle.fullDataMigration");
               break;
            case "ensureObjectAccessibility":
               this.decommissionMode = DecommissionMode.ensureObjectAccessibility;
               this.secondaryTitle = VsanUiUtils.getString(
                     "vsan.dialog.precheckEvacuation.detailedReport.secondaryTitle.ensureAccessibility");
               break;
            case "noAction":
               this.decommissionMode = DecommissionMode.noAction;
               this.secondaryTitle = VsanUiUtils.getString(
                     "vsan.dialog.precheckEvacuation.detailedReport.secondaryTitle.noDataMigration");
               break;
            default:
               Logger.error("Unknown decommissionMode type passed: " + context.selectedDecommisionMode);
               break;
         }
      } else {
         // Dialog is opened via Disk Management "Pre-check" action
         this.decommissionMode = DecommissionMode.ensureObjectAccessibility;
         this.primaryTitle = VsanUiUtils.getString("vsan.dialog.precheckEvacuation.title");
         this.secondaryTitle = this.entityName;
      }
   }

   onDecommissionModeSummaryChanged = (summary: DecommissionModeSummary) => {
      // In case the precheck dialog doesn't show any objects, decrease the height
      if  (VsanDiskMgmtActionsUtil.isPrecheckWithObjects(summary)) {
         VsanUiUtils.setModalDialogHeight(PreCheckEvacuationDialogSize.HEIGHT_WITH_OBJECTS);
      } else {
         VsanUiUtils.setModalDialogHeight(PreCheckEvacuationDialogSize.HEIGHT_WITHOUT_OBJECTS);
      }
   };
}